<form @submit.prevent="registerlabourDebt()">
  <div class="row" v-if="addlabour">

    <div class="col-md-3">
      <div class="form-group">
        <label class="form-label">Select Labour</label>
        <Multiselect
          v-model="labour"
          :options="laboursList"
          label="name"
          placeholder="Select Labour"
          :searchable="true"
          :allow-empty="false"
        />
      </div>
    </div>


    <div class="col-md-2">
      <div class="form-group">
        <label class="form-label">Date Debted</label>
        <input
          type="date"
          class="form-control"
          v-model="debt_date"
          required
        >
      </div>
    </div>

        <div class="col-md-3">
      <div class="form-group">
        <label class="form-label">Amount </label>
        <input
          type="number"
          class="form-control"
          v-model="amount"
          required
          step="any"
          placeholder="Enter Amount"
        >
      </div>
    </div>

            <div class="col-md-10">
      <div class="form-group">
        <label class="form-label">Any Description</label>
