                            <template #actions="props">

                                <a href="javascript:void(0);" class="text-primary" @click="OpenlabourViewModal(props.row)"  data-bs-toggle="modal" data-bs-target="#labour-view-modal" >
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-eye" viewBox="0 0 16 16">
