            </div>
        </div>


<!-- labour View modal starts -->
<div class="modal fade mymodal" id="labour-view-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="labourViewModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="labourViewModalLabel">labour DEBT FOR {{ selectedDebt?.labour }}</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        <!-- labour Information -->
        <div class="row mb-3" v-if="selectedDebt">
          <div class="col-md-12">
             <div class="panel p-0">
            <div class="custom-table table3 table-responsive">
              <table class="table table-hover table-striped">
                <thead>
                  <tr>
                    <th>labour Name</th>
                    <th>Amount</th>
                    <th>Status</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>{{ selectedDebt.labour }}</td>
                    <td>{{ selectedDebt.amount }}</td>
                    <td>
                      <span class="badge inv-status" :class="'badge-' + selectedDebt?.status?.color">
                        {{ selectedDebt?.status?.name }}
                      </span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            </div>
          </div>
        </div>
        
        <!-- Attachment Section -->
        <div class="d-flex justify-content-between align-items-center mb-3">
          <h5 class="mb-0"></h5>
          <button type="button" class="btn  btn-sm mx-2" :class="showAttachmentForm ?'btn-danger':'btn-primary'" @click="showAttachmentForm = !showAttachmentForm">
            <svg
            v-if="!showAttachmentForm"
            xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-paperclip me-1" viewBox="0 0 16 16">
              <path d="M4.5 3a2.5 2.5 0 0 1 5 0v9a1.5 1.5 0 0 1-3 0V5a.5.5 0 0 1 1 0v7a.5.5 0 0 0 1 0V3a1.5 1.5 0 1 0-3 0v9a2.5 2.5 0 0 0 5 0V5a.5.5 0 0 1 1 0v7a3.5 3.5 0 1 1-7 0z"/>
            </svg>
            <span v-else>X</span>
          </button>
        </div>

        <!-- Attachment Form -->
        <div v-if="showAttachmentForm" class="row mb-3">
          <div class="col-md-12">
            <form @submit.prevent="uploadRepaymentAttachment()">
              <div class="row">
                <div class="col-md-2">
                  <label class="form-label">Enter Amount</label>
                  <input 
                    type="text" 
                    class="form-control" 
                    v-model="amount"
                    placeholder="Enter Amount"
                  >
                </div>
                <div class="col-md-4">
                  <label class="form-label">Paid To</label>
                    <multiselect 
                v-model="account"
                :options= "accounts"
                placeholder="Select Account"
                label="name"
                :searchable = "true"        
                multple="false"
                :required="true"
                :allow-empty="false"
           />           
                </div>
                 <div class="col-md-3">
                  <label class="form-label">Date</label>
                  <input 
                    type="date" 
                    class="form-control" 
                    v-model="date"
                  >
                </div>
                <div class="col-md-3">
                  <label class="form-label">Attachment (PDF only)</label>
                  <input 
                    type="file" 
                    class="form-control" 
                    @change="handleRepaymentAttachment"
                    accept="application/pdf"
                    required
                  >
                </div>
              </div>
              <div class="row mt-3">
                <div class="col-md-12 text-end">
                  <button type="button" class="btn btn-secondary me-2" @click="cancelAttachmentUpload()">
                    Cancel
                  </button>
                  <button type="submit" class="btn btn-primary" :disabled="uploadinglabourAttachment">
                    <span v-if="uploadinglabourAttachment" class="spinner-border spinner-border-sm text-white me-2" role="status"></span>
                    Save
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>

        <!-- labour Attachments Table -->
        <h5 class="mb-3">labour Debt Payments</h5>
        <div class="row mb-3">
          <div class="col-md-12">
            <div class="panel p-0">
                <div v-if="loadingAttachments" v-html="tableShimmer()"></div>
              <div v-else class="custom-table table3">
                <v-client-table 
                  :data="debtPayments" 
                  :columns="attachmentColumns" 
                  :options="table_option"
                >
                  <template #actions="props">
                   <a :href="props.row.attachment" 
   target="_blank" 
   rel="noopener noreferrer">
    <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-file-earmark-pdf text-primary" viewBox="0 0 16 16">
  <path d="M14 14V4.5L9.5 0H4a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2M9.5 3A1.5 1.5 0 0 0 11 4.5h2V14a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h5.5z"/>
  <path d="M4.603 14.087a.8.8 0 0 1-.438-.42c-.195-.388-.13-.776.08-1.102.198-.307.526-.568.897-.787a7.7 7.7 0 0 1 1.482-.645 20 20 0 0 0 1.062-2.227 7.3 7.3 0 0 1-.43-1.295c-.086-.4-.119-.796-.046-1.136.075-.354.274-.672.65-.823.192-.077.4-.12.602-.077a.7.7 0 0 1 .477.365c.088.164.12.356.127.538.007.188-.012.396-.047.614-.084.51-.27 1.134-.52 1.794a11 11 0 0 0 .98 1.686 5.8 5.8 0 0 1 1.334.05c.364.066.734.195.96.465.12.144.193.32.2.518.007.192-.047.382-.138.563a1.04 1.04 0 0 1-.354.416.86.86 0 0 1-.51.138c-.331-.014-.654-.196-.933-.417a5.7 5.7 0 0 1-.911-.95 11.7 11.7 0 0 0-1.997.406 11.3 11.3 0 0 1-1.02 1.51c-.292.35-.609.656-.927.787a.8.8 0 0 1-.58.029m1.379-1.901q-.25.115-.459.238c-.328.194-.541.383-.647.547-.094.145-.096.25-.04.361q.016.032.026.044l.035-.012c.137-.056.355-.235.635-.572a8 8 0 0 0 .45-.606m1.64-1.33a13 13 0 0 1 1.01-.193 12 12 0 0 1-.51-.858 21 21 0 0 1-.5 1.05zm2.446.45q.226.245.435.41c.24.19.407.253.498.256a.1.1 0 0 0 .07-.015.3.3 0 0 0 .094-.125.44.44 0 0 0 .059-.2.1.1 0 0 0-.026-.063c-.052-.062-.2-.152-.518-.209a4 4 0 0 0-.612-.053zM8.078 7.8a7 7 0 0 0 .2-.828q.046-.282.038-.465a.6.6 0 0 0-.032-.198.5.5 0 0 0-.145.04c-.087.035-.158.106-.196.283-.04.192-.03.469.046.822q.036.167.09.346z"/>
</svg>
</a>
                  </template>
                </v-client-table>
              </div>
            </div>
          </div>
        </div>
        
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
      </div>
    </div>
  </div>
</div>
<!-- labour View modal ends -->

    </div>
</template>
<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>
<script setup>
    import { onMounted, ref ,inject } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    useMeta({ title: 'Custom Table' });
import Multiselect from '@suadelabs/vue3-multiselect';
import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

     const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const showMessage = inject('showMessage')
    const showError = inject('showError')
    const formatNumber = inject('formatNumber')
    const formatDate = inject('formatDate')
    const printlabourList = inject('printlabourList')
    const tableShimmer = inject('tableShimmer');
    const loading_spinner = ref(false);
    const columns = ref(['Sno', 'labour','amount','repaid','balance','description','debt_date', 'status','actions']);
    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50 , 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['id', 'service_name'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const company_id = ref('')

    const addlabour = ref(false)    
    const labour = ref('')
    const labourDebts = ref([])

  const getlabourDebts = async () => {
        loading_spinner.value = true;
        try{
        const response = await axiosInstance.get(`labour-debts?company_id=${company_id.value}`)
        labourDebts.value = response.data.map((item,index)=>({
         ...item,
         Sno : index + 1,
         amount : formatNumber (item.amount),
         labour : item.labour.name,
         debt_date : item.date,
         monthly_payable_original : item.monthly_payable,
         monthly_payable : formatNumber(item.monthly_payable),
         amount_original : item.amount,
         repaid : formatNumber(item.repayments_sum_amount),
         balance : formatNumber(item.amount - item.repayments_sum_amount)

        }))
        }catch(error){
            console.log(error)
            showError(error.message)
        }finally{
            loading_spinner.value = false;
        }
    };

    const debt_type = ref('')
    const debt_date = ref('')
    const amount = ref('')
    const description = ref('')
    const date = ref('')
    const monthly_payable = ref('')
    const registerlabourDebt = async() => {
      loading_spinner.value = true
        const payload = {
            labour_id : labour.value.id,
            amount : amount.value,
            date: debt_date.value,
            description : description.value,
        }
        try {
            const response = await axiosInstance.post('labour-debts', payload);
            showMessage('Debt Saved');
            addlabour.value = !addlabour.value;
            await getlabourDebts();           
            labour.value = '';
            amount.value = '';
            debt_date.value = '';
        } catch (error) {
            showError(error.message);
            console.log(error);
        }
    };

    const selectedDebt = ref(null)
    const OpenlabourViewModal = async (row) => {
        selectedDebt.value = row;
        amount.value = selectedDebt.value.monthly_payable_original
        await getdebtPayments(row.id);
    };


    const showAttachmentForm = ref(false);
const attachmentDescription = ref('');
const labourAttachment = ref(null);
const uploadinglabourAttachment = ref(false);

const handleRepaymentAttachment = (e) => {
    labourAttachment.value = e.target.files[0];
};

const uploadRepaymentAttachment = async () => {
    uploadinglabourAttachment.value = true;

    const formData = new FormData();
    formData.append('labour_id', selectedDebt.value.labour_id);
    formData.append('host_id', selectedDebt.value.labour_id);
    formData.append('host', selectedDebt.value.labour);
    formData.append(
      'reference_number',
      'salary on ' + date.value
     );
    formData.append('labour_debt_id', selectedDebt.value.id);
    formData.append('amount', amount.value);
    formData.append('date', date.value);
    formData.append('direction', 'INCREMENT');
    formData.append('account_transaction_point_id', account.value.id);
    formData.append('opening_balance', account.value.transactions_sum);
    formData.append('issuer_id', JSON.parse(localStorage.getItem("user")).id);
    formData.append('attachment', labourAttachment.value);

  
    try {
        const response = await axiosInstance.post(
            'labour-debt-repayments',
            formData,
            { headers: { 'Content-Type': 'multipart/form-data' } }
        );
        
        showMessage('Debt Updated!');
        await getReactivedebtPayments(selectedDebt.value.id);
        getlabourDebts()
        cancelAttachmentUpload();
        
    } catch (error) {
        showError(error.message);
        console.log(error);
    } finally {
        uploadinglabourAttachment.value = false;
    }
};

const cancelAttachmentUpload = () => {
    showAttachmentForm.value = false;
    attachmentDescription.value = '';
    labourAttachment.value = null;
    const fileInput = document.querySelector('input[type="file"]');
    if (fileInput) fileInput.value = '';
};


const debtPayments = ref([]);
const attachmentColumns = ref(['Sno',  'date', 'amount', 'actions']);

const loadingAttachments = ref(false);
const getdebtPayments = async (id) => {
    loadingAttachments.value = true;
    try {
        const response = await axiosInstance.get(`labour-debt-repayments?labour_debt_id=${id}`);
        debtPayments.value = response.data.map((item, index) => ({
            ...item,
            Sno: index + 1,
            attachment : process.env.VUE_APP_API_BASE_URL + '../../' + item.transaction.attachment,  
            date: formatDate(item.created_at),
            amount : formatNumber(item.amount),
            description: item.remark,
        }));
    } catch (error) {
        showError(error.message);
        console.log(error);
    }finally{
        loadingAttachments.value = false; 
    }
};

 const getReactivedebtPayments = async (id) => {
      const response = await axiosInstance.get(`labour-debt-repayments?labour_debt_id=${id}`); 
        debtPayments.value = response.data.map((item, index) => ({
            ...item,
            Sno: index + 1,
            attachment: process.env.VUE_APP_API_BASE_URL + '../../' + item.attachment,  
            date: formatDate(item.created_at),
            description: item.remark,
            amount : formatNumber(item.amount)
        }));

 }

 const laboursList = ref([''])
  const getLabours = async () => {
      const response = await axiosInstance.get(`labours?company_id=${company_id.value}`); 
        laboursList.value = response.data
 }

   const getData = (company)=>{
        company_id.value = company
        getlabourDebts(); 
        getLabours()
   }

   const accounts = ref([])
const account = ref('')
const getAccounts = async()=>{
          try {
            const response = await axiosInstance.get('account-transaction-points');
            accounts.value = response.data.map((item, index) => ({
                ...item,
name: 
  (item?.name ?? '') + ' ' +
  (item?.mobile_money?.name ?? '') + ' ' +
  (item?.mobile_money?.category ?? 'CASH')
            }));
        } catch (error) {
            showError(error.message);
        }finally{

        }
}


    onMounted(async () => {
       getAccounts()
    });


</script>
